.video-modal-ssv {
    height: 80vh;
}

.video-modal-ssv-row {
    display: flex;
    height: 100%;
}

.video-modal-ssv-col {
    width: 50%;
    height: 80vh;
}

.modal_video_col {
    /* object-fit: cover; */
    height: 100%;

    width: 100%;
    overflow: hidden;
    border-radius: 7px 0px 0px 7px;

}

.video-modal-ssv-col video {
    object-fit: fill;
    height: 80vh;

}

.comment-section-main-div-ssv {
    height: 100%;
    /* background-color: red; */
}

.section_comment_scroll {
    /* background-color: #26AE4C; */
    height: calc(100% - 46px);
    overflow: auto;
}

.pinned_section_closed {
    background: #F3F4F8;
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 1;
    cursor: pointer;
    padding: 5px;
}

.pinned_section_closed p {
    margin-bottom: 0px !important;
}

.pinned_section_closed label {
    height: 16px;
    min-width: 16px;
    font-size: 10px;
    line-height: 16px;
    background: #334499;
    color: #ffffff;
    margin: 0 !important;
    padding: 0 !important;
    margin-left: 5px !important;
    border-radius: 8px;
    display: inline-block;
    vertical-align: text-bottom;
    text-align: center;
    padding: 0 !important;
}